.body1 {
  color: #fff;
  overflow-x: hidden;
  height: 90%;
  background-image: linear-gradient(#81D4FA, #6415ff);
  background-repeat: no-repeat;
}

.card0 {
  width:90%;
}

.card1 {
  background-image: linear-gradient(#6415ff, #81D4FA);
  padding: 30px 20px 30px 50px;
}

.image {
  width: 300px;
  height: 350px;
}

.large-font {
  font-size: 70px;
  font-family: Arial;
}

.fa-sun-o {
  font-size: 22px;
}

.card2 {
  background-color: #608090;
  padding: 0px 0px 40px 40px;
}

input {
  background-color: #608090;
  padding: 24px 0px 12px 0px ;
  width: 80%;
  box-sizing: border-box;
  border: none ;
  border-bottom: 1px solid #CFD8DC ;
  font-size: 16px ;
  color: #fff ;
}

::placeholder {
  color: #B0BEC5 ;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #B0BEC5 ;
}

::-ms-input-placeholder {
  color: #B0BEC5 ;
}

.light-text {
  color: #B0BEC5;
}

.suggestion:hover {
  color: #fff;
  cursor: pointer;
}

.line {
  height: 1px;
  background-color: #B0BEC5;
}

@media screen and (max-width: 500px) {
  .card1 {
      padding-left: 26px;
  }
}












@media(max-width:1000px){
  .right{
    display: none;
  }
  #h4{
    color:#3c0d99;
    font-size:34px;
    font-weight: bolder;
  }
  .rightbox{
    display:none;
  }

  .boxonimg{
    display: none;
  }
}
@media(max-width:770px){
.boxes1{
  justify-content: center;
  display: flex;
  flex-direction: column;
}
.boxes2{
  display: none;
}
.rightS{
  display: none;
}
}